import { CircularProgress, Paper, Theme, Typography } from '@material-ui/core';
import React, { ReactNode, useCallback, useEffect } from 'react';

import { useState } from 'react';

interface IQuote {
  content: string;
  author: string;
}

export interface IRandomQuoteProps {}

const RandomQuote = ({}: IRandomQuoteProps) => {
  const [quote, setQuote] = useState<IQuote>();
  const [isLoadingQuote, setIsLoadingQuote] = useState<boolean>(true);
  const [hasApiError, setHasApiError] = useState<boolean>(false);

  const fetchQuote = useCallback(async () => {
    await fetch('https://api.quotable.io/random')
      .then(async (response) => {
        const randomQuote: IQuote = await response.json();
        setQuote({ ...randomQuote });
      })
      .catch(() => {
        setHasApiError(true);
      })
      .finally(() => {
        setIsLoadingQuote(false);
      });
  }, []);

  useEffect(() => {
    fetchQuote();
  }, [fetchQuote]);

  return hasApiError ? (
    <></>
  ) : (
    <>
      <Paper
        elevation={4}
        sx={{
          p: 4,
          color: (theme: Theme) => theme.palette.text.secondary,
        }}>
        {isLoadingQuote && (
          <Typography textAlign="center">
            <CircularProgress
              size={16}
              sx={{
                color: (theme: Theme) => theme.palette.text.secondary,
              }}
            />
          </Typography>
        )}
        {!isLoadingQuote && quote && (
          <>
            <Typography textAlign="center" variant="subtitle1">
              <i>
                {'❝ '}
                {quote.content}
                {' ❞'}
              </i>
            </Typography>
            <Typography textAlign="center" variant="subtitle2">
              - {quote.author}
            </Typography>
          </>
        )}
      </Paper>
    </>
  );
};

export default RandomQuote;
