import PageContentLayout from '../layout/PageContentLayout';
import PageLayout from '../layout/PageLayout';
import RandomQuote from '../components/RandomQuote';
import React from 'react';
import StyledNavLink from '../components/StyledNavLink';
import { Typography } from '@material-ui/core';
import personalInfo from '../data/personalInfo';

interface IHomeProps {}

const Home = ({}: IHomeProps) => {
  return (
    <>
      <PageLayout
        title={personalInfo.fullName}
        enableTitleTemplate={false}
        description="A BEAUTIFUL, RESPONSIVE, STATICALLY-GENERATED, REACT APPLICATION WRITTEN WITH MODERN JAVASCRIPT and HOSTED OVER THE GITHUB PAGES">
        <PageContentLayout
          header="About this site"
          subHeader="A BEAUTIFUL, RESPONSIVE, STATICALLY-GENERATED, REACT APPLICATION WRITTEN WITH MODERN JAVASCRIPT and HOSTED OVER THE GITHUB PAGES">
          <Typography variant="body1" paragraph>
            Welcome to my website!
          </Typography>
          <Typography variant="body1" paragraph>
            Thanks for visiting! 🙏 Explore this site to read more{' '}
            <StyledNavLink to="/about" label="about my background" />,{' '}
            <StyledNavLink to="/resume" label="what I have to offer 🧾" />,{' '}
            <StyledNavLink to="/projects" label="work I've done 👷‍♂️" />, view{' '}
            <StyledNavLink to="/stats" label="site statistics 🧮" />, or If you
            have questions, would like to discuss an opportunity to work
            together or just catch-up over coffee,{' '}
            <StyledNavLink
              to="/contact"
              label="feel free to get in touch with me ✍"
            />
            .
          </Typography>
          <RandomQuote />
        </PageContentLayout>
      </PageLayout>
    </>
  );
};

export default Home;
